<template>
	<div :class="$style.nav7">
		<div :class="$style.titleImg">
			<img src="./title0.png" />
		</div>
		<box :class="[$style.box,$style.box0]">
			<div :class="$style.pendant"></div>
			<span slot="title"></span>
			<p v-for="(v,i) in data.content0" v-html="v" :class="$style.p"></p>
		</box>
		<div :class="$style.titleImg">
			<img src="./title1.png" />
		</div>
		<box :class="[$style.box,$style.box1]">
			<stars :data="data.content1"></stars>
		</box>
		<div :class="$style.titleImg">
			<img src="./title2.png" />
		</div>
		<box :class="[$style.box,$style.box2]">
			<div :class="$style.pendant"></div>
			<p v-for="(v,i) in data.content2" v-html="v" :class="$style.p"></p>
		</box>
		<div :class="$style.titleImg">
			<img src="./title3.png" />
		</div>
		<box :class="[$style.box,$style.box3]">
			<div :class="$style.pendant"></div>
			<p v-for="(v,i) in data.content3" v-html="v" :class="$style.p"></p>
		</box>
		<div :class="$style.titleImg">
			<img src="./title4.png" />
		</div>
		<box :class="[$style.box,$style.box4]" v-for="(v,i) in data.content4" :key="i">
			<p v-html="v" :class="$style.p"></p>
		</box>
		<div :class="$style.titleImg">
			<img src="./title5.png" />
		</div>
		<box :class="[$style.box,$style.box5]">
			<ul>
				<li v-for="(v,i) in data.content5">
					<RingProgressBar :value="v.value" :color="color[i]" :key="'RingProgressBar'+i"></RingProgressBar>
					<p :class="$style.title" :style="{color:color[i]}">{{v.title}}</p>
				</li>
			</ul>
		</box>
		<box :class="[$style.box,$style.box6]" v-for="(v,i) in data.content5" :key="'box6'+i">
			<p :class="$style.title">
				<span>{{v.title}}</span>
			</p>
			<div :class="[$style.value,$style['color'+i]]">{{v.value}}</div>
			<p v-html="v.content" :class="$style.p"></p>
		</box>
	</div>
</template>

<script>
	var component = {
		name: 'nav7', //
		props: {

		},
		data() {
			return {
				color:['#d70707','#f05d2a','#4d69cb'],
				data: {
					content0: [
						'',
'',
					],
					content1: [{
							title: '',
							content: [''],
							type: 0,
						}, {
							title: '',
							content: ['','','','',''],
							type: 1,
						}, {
							title: '',
							content: ['',''],
							type: 0,
						},
						{
							title: '',
							content: ['','','','','','','','','',''],
							type: 1,
						}
					],
					content2: [
						'hi',
					],
					content3: [
						'',
					],
					content4: [
						'',
					],
					content5: [{
						title: '  ',
						value: 90,
						content: '',
					}, {
						title: '',
						value: 88,
						content: '',
					}, {
						title: '',
						value: 88,
						content: '',
					} ],
				},
			};
		},
		computed: { //
			data_computed() {
				return {

				}
			}
		},
		watch: { //

		},
		components: { //
			titleImg() {
				return import('../titleImg')
			},
			box() {
				return import('../box')
			},
			stars() {
				return import('../stars')
			},
			RingProgressBar(){
				return import('../RingProgressBar')
			}
			
		},
		methods: { //

		},
		filters: { //

		},
		beforeRouteEnter(to, fo, next) { //
			next();
		},
		beforeCreate() { //vue

		},
		created() { //

		},
		beforeMount() { //

		},
		mounted() { //

		},
		beforeRouteUpdate(to, fo, next) { //
			next();
		},
		beforeUpdate() { //

		},
		updated() { //

		},
		beforeRouteLeave(to, fo, next) { //
			next();
		},
		beforeDestroy() { //

		},
		destroyed() { //

		},
	};
	export default component;
</script>

<style module lang="stylus">
	.nav7 {
		padding-left: .5rem;
		padding-right: @padding-left;
		overflow-x: hidden;
		.title-img {
			margin-top: .5rem;
			img {
				width: 100%;
				display: block;
			}
		}
		.box {
			margin-top: 1rem;
			.p {
				position: relative;
				strong {
					color: red;
					text-decoration: underline;
				}
			}
			&.box0 {
				margin-top: 2rem;
				.pendant {
					width: 5.175rem;
					height: 2.65rem;
					background-image: url(./pendant0.png);
					background-size: 100% 100%;
					position: absolute;
					bottom: 0;
					right: 0;
					z-index: 0;
					transform: translate(10%, 0%);
				}
			}
			&.box1 {
			}
			&.box2{
				.pendant {
					width:5.675rem;
					height: 3.1rem;
					background-image: url(./pendant1.png);
					background-size: 100% 100%;
					position: absolute;
					bottom: 0;
					left: 0;
					z-index: 0;
					transform: translate(0%, 50%);
				}
			}
			&.box3{
				padding-bottom: 2rem;
				.pendant {
					width: 5.475rem;
					height:4.875rem;
					background-image: url(./pendant2.png);
					background-size: 100% 100%;
					position: absolute;
					bottom: 0;
					right: 0;
					z-index: 0;
					transform: translate(10%, 10%);
				}
			}
			&.box2,
			&.box3,
			&.box4 {
				.p {
					strong {
						color: red;
						text-decoration: underline;
					}
					+.p {
						margin-top: .5rem;
					}
				}
			}
			&.box5{
				ul{
					display: flex;
					flex-wrap: wrap;
					>li{
						width: calc((100% - 2rem) / 3);
						.title{
							margin-top: .5rem;
							font-size: 0.75rem;
						}
						+li{
							margin-left: 1rem;
						}
					}
				}
				
			}
			&.box6 {
				.title {
					text-align: center;
					span {
						display: inline-block;
						position: relative;
						padding: 0 2rem;
						&:after {
							content: '';
							display: inline-block;
							width: 1.75rem;
							height: 0.9rem;
							background-image: url(../TransverseProgressBarTitle/titleIcon.png);
							background-size: 100% 100%;
							position: absolute;
							top: 50%;
							right: 0;
							transform: translate(0, -50%);
						}
						&:before {
							content: '';
							display: inline-block;
							width: 1.75rem;
							height: 0.9rem;
							background-image: url(../TransverseProgressBarTitle/titleIcon.png);
							background-size: 100% 100%;
							position: absolute;
							top: 50%;
							left: 0;
							transform: translate(0, -50%)rotateY(180deg);
						}
					}
				}
				.value{
					width: 7.275rem;
					height: 1.8rem;
					background-size: 100% 100%;
					margin: auto;
					line-height:@height ;
					font-size: 1.2rem;
					color: white;
					text-align: center;
					margin-top: .5rem;
					margin-bottom: .5rem;
					&.color0{
						background-image: url(color0.png);
					}
					&.color1{
						background-image: url(color1.png);
					}
					&.color2{
						background-image: url(color2.png);
					}
					
				}
				.p {
					strong {
						color: red;
						text-decoration: underline;
					}
				}
			}
		}
	}
</style>


